.container {
    max-width: 93.5rem;
    margin: 0 auto;
    padding: 0 2rem;
    background-image: url(/static/media/lamp.7c5c8777.jpg);
}
.p{
    font-size: 20px;
}

.btn {
    display: inline-block;
    font: inherit;
    background: none;
    border: none;
    color: inherit;
    padding: 0;
    cursor: pointer;
}

.visually-hidden {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
}

/* Profile Section */

.profile {
    padding: 5rem 0;
}

.profile::after {
    content: "";
    display: block;
    clear: both;
}

.profile-image {
    float: left;
    width: calc(33.333% - 1rem);
    display: flex;
    /* justify-content: center;*/
    align-items: center; 
    /* margin-right: 3rem; */
    border-radius: 50%;
}

.profile-image img {
    border-radius: 50%;
}

.profile-user-settings,
.profile-stats,
.profile-bio {
    float: left;
    width: calc(66.666% - 2rem);
}

.profile-user-settings {
    margin-top: 1.1rem;
}

.profile-user-name {
    display: inline;
    font-size: 3.2rem;
    font-weight: 300;
    margin: auto;
}

.profile-edit-btn {
    font-size: 1.4rem;
    line-height: 1.8;
    border: 0.1rem solid #dbdbdb;
    border-radius: 0.3rem;
    padding: 0 2.4rem;
    margin-left: 2rem;
}

/* .profile-settings-btn {
    font-size: 2rem;
    margin-left: 1rem;
} */

.profile-stats li:last-of-type {
    margin-right: 0;
}
